import request from './request'

export const getGoods = (data) => {
  return request({
    url: 'goods',
    method: 'get',
    params: data
  })
}
export const addGoods = (data) => {
  return request({
    url: 'goods',
    method: 'post',
    data: data
  })
}
export const getIdGoods = (id) => {
  return request({
    url: `goods/${id}`,
    method: 'get'
  })
}
export const editGoods = (id, data) => {
  return request({
    url: `goods/${id}`,
    method: 'put',
    data: data
  })
}
export const deleteGoods = (id) => {
  return request({
    url: `goods/${id}`,
    method: 'delete'
  })
}
export const syncAttr = (id) => {
  return request({
    url: `goods/${id}/attributes`,
    method: 'put'
  })
}
